


import React, { useState, useEffect, useContext } from "react";
import { Link, Outlet, useLocation, useNavigate } from "react-router-dom";

import styled from "styled-components";
import { Footer } from "../components/Footer/Footer";
import { LanguageContext } from "../context/languageContext";



const IntroWrapper = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
`;

const IntroImgContainer = styled.div`
  height: 400px;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  position: relative;
  img{
    height: 400px;
    width: 100%;
    object-fit: cover;
  }
`;

const IntroContentContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* border: 1px solid #cecece; */
  background-color: #ffffff;
  @media (min-width: 1536px) {
    max-width: 1536px;
    margin: 0 auto; /*    */
  }
`;

const TabContainer = styled.div`
  width: 50%;
  height: 50px;
  bottom: 0px;
  display: flex;
  justify-content: center;
  flex-direction: row;
  position: absolute;
  
  div{
    background-color: #e5e5e5;
    flex: 1;
    justify-content: center;
    align-items: center;
    display: flex;
    border-right: 0.5px solid #c4c4c4;
    padding: 10px;
    cursor: pointer;
    span{
      min-width: 80px;
      font-size: 16px;
      font-weight: 600;
      color: #6f6f6f;
      display: flex;
      justify-content: center;
    }
  }
  @media (max-width: 450px) {
    margin-left: 0px;
    width: 100%;
    bottom: 0px;
    position: absolute;
    justify-content: center;
    align-items: center;
    div {
      height: 30px;
      span{
        min-width: 0px;
        font-size: 16px;
        font-weight: 600;
        color: #6f6f6f;
        display: flex;
        justify-content: center;
      }
    }
  }
`;


export const Introduce = () => {
  const { isLanguage, setIsLanguage } = useContext(LanguageContext);
  const [ currentPage, setCurrentPage ] = useState([{ page:'', index: 0 }]);
  const navigate = useNavigate();
  const currentURL = useLocation();

  useEffect(()=>{
    if(currentURL?.pathname === '/intro/company'){
      setCurrentPage([{ page:'', index: 0}]);
    };
    if(currentURL?.pathname === '/intro/history'){
      setCurrentPage([{ page:'', index: 1}]);
    };
    if(currentURL?.pathname === '/intro/vision'){
      setCurrentPage([{ page:'', index: 2}]);
    };
    if(currentURL?.pathname === '/intro/awards'){
      setCurrentPage([{ page:'&', index: 3}]);
    };
  },[currentURL])

  const currentPageHandler = (pageName, index) => {
    setCurrentPage(() => {
      return [{ page: pageName, index: index }]
    });
  };

  return (
      <>
        <IntroWrapper>
          <IntroImgContainer>
            {/*      */}
            <img src={ process.env.PUBLIC_URL + "/img/companies.png"} alt="companies"></img>
            <TabContainer>
              <div 
                onClick={()=>{
                  currentPageHandler(''); 
                  navigate('/intro/company');
                }}
                style={{
                  backgroundColor: `${ currentPage[0].page === "" ? "#4975db" : "#e5e5e5" }`,
                  fontWeight: "800"
                }}
              >
                <span style={{
                  color: `${ currentPage[0].page === "" ? "white" : "#6f6f6f" }`,
                  fontWeight: `${ currentPage[0].page === "" ? "800" : "600" }`,
                }}>{isLanguage === 20 ? "":"Intro" }</span>
              </div>

              <div 
                onClick={()=>{
                  currentPageHandler('')
                  navigate('/intro/history');
                }}
                style={{
                  backgroundColor: `${ currentPage[0].page === "" ? "#4975db" : "#e5e5e5" }`,
                  fontWeight: "800"
                }}
              >
                <span style={{
                  color: `${ currentPage[0].page === "" ? "white" : "#6f6f6f" }`,
                  fontWeight: `${ currentPage[0].page === "" ? "800" : "600" }`,
                }}>{isLanguage === 20 ? "" : "History"}</span>
              </div>

              <div 
                onClick={()=>{
                  currentPageHandler('');
                  navigate('/intro/vision');
                }}
                style={{
                  backgroundColor: `${ currentPage[0].page === "" ? "#4975db" : "#e5e5e5" }`,
                  fontWeight: "800"
                }}
              >
                <span style={{
                  color: `${ currentPage[0].page === "" ? "white" : "#6f6f6f" }`,
                  fontWeight: `${ currentPage[0].page === "" ? "800" : "600" }`,
                }}>{isLanguage === 20 ? "" : "Vision"}</span>
              </div>

              <div 
                onClick={()=>{
                  currentPageHandler('&');
                  navigate('/intro/awards')
                }}
                style={{
                  backgroundColor: `${ currentPage[0].page === "&" ? "#4975db" : "#e5e5e5" }`,
                  fontWeight: "800"
                }}
              >
                <span style={{
                  color: `${ currentPage[0].page === "&" ? "white" : "#6f6f6f" }`,
                  fontWeight: `${ currentPage[0].page === "&" ? "800" : "600" }`,
                }}>{isLanguage === 20 ? "&":"Awards" }</span>
              </div>
            </TabContainer>
          </IntroImgContainer>
          
          {/*    */}
          <IntroContentContainer>
            <Outlet/>
            <Footer/>
          </IntroContentContainer>

        </IntroWrapper>
      </>
  )
}